@import url("https://fonts.googleapis.com/css?family=Montserrat");


.leaderboard-container {
  width: 550px;
  margin: 80px auto; }
.col-xs-1{
    width:10%;
}
.col-xs-3{
    width:30%;
}
.col-xs-5{
    width:50%;
}
.leadheader {
  width: 100%;
  height: 70px;
  background: rgba(0, 168, 232, 0.79);
  text-align: center;
  border-radius: 15px 15px 0 0; }
  .leadheader h2 {
    margin: 0;
    color: #fefefe;
    padding-top: 20px; }

.colheader {
  background: rgba(254, 254, 254, 0.9);
  width: 100%;
  margin-left: 0;
  padding:10px 0;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2); }
  .colheader h4 {
    font-size: 15px; }
  .colheader .username {
    text-align: left;
    user-select: none; }
  .colheader .totalLike {
    cursor: pointer;
    user-select: none; }

.users {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  background: #fefefe;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 5px;
  text-align: center; }
  .text-align-left{
      text-align: left;
  }
  .leaderboard-container a > div{
      background-color:#fff;
      transition:background-color 0.5s;
  }

  .leaderboard-container .total{
      font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 19px;
  }
  .leaderboard-container a:hover > div{
    background-color: #474747;
    color:#fff;
  }
  .leaderboard-container a:hover .total,.leaderboard-container a:hover .fa-heart{
      color: #fff!important;
  }
  .users .name {
    text-align: left; }
    .users .name a {
      padding-top: 15px;
      vertical-align: middle; }
  .users img {
    /* float: left; */
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 2px solid rgba(0, 168, 232, 0.79);
    margin-right: 10px; }
  .users .rank {
    padding-left: 6px; }
  .users:nth-of-type(odd) {
    background: rgba(254, 254, 254, 0.9); }
  .users:nth-of-type(even) {
    background: rgb(201 201 201 / 95%); }
  .users:last-child {
    border-radius: 0 0 5px 5px; }

@media (max-width: 550px) {
  .users .name {
    padding-left: 5px; }
  .leaderboard-container {
    width: 100%; } }

@media (max-width: 540px) {
  .leaderboard-container {
    padding: 0; }
  .colheader div[class^='col-xs'] {
    padding: 0; }
  .users {
    text-align: center; }
    .users img {
      display: block;
      float: none;
      margin: 0 auto;
      width: 35px;
      height: 35px; }
    .users .name {
      text-align: center; }
      .users .name a {
        display: block;
        padding-top: 4px; } }
