.navbar {
  z-index: 1;
  position: fixed;
  top: 0;
  width: 100%;
  height: auto;
  white-space: nowrap;
  background: #333;
  z-index: 2;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
}
.navbar-nav{
  display:flex;
  align-items: center;
}
.navbar-nav .wait-txt{
  font-size: 18px;
  color: azure;
  background: #4b0404;
  padding: 10px;
  border-radius: 15px;
}
.navbar-brand{
  transform:scale(1.2);
  transition:transform 0.6s;
  font-family:cursive;
}
.navbar-brand:hover{
  transform:scale(1.4);

}
.navbar-nav .finishTime{
  font-size: 22px;
  color:yellow;
}
.logout{
cursor: pointer;
}