.card-c {
    display:flex;
    justify-content:center;
    margin: 30px;
    min-height: 38.5rem;
  }

  .chat-text-field{
    background:#fff;
    padding:7px 60px 7px 7px;
    width:max-content;
    border-radius:10px;
    border-top-left-radius: 0px;
    margin:14px 0;
  }
.chat-card{
  width:65%;
  padding:5px 10px;
  display:flex;
  flex-direction:column-reverse;
  justify-content: flex-end;
  align-items: center;
  background-color:#333;
  border-radius: 15px;
  margin-bottom: 15px;
}
.chat-card .btn-send, .fa-paper-plane{
  font-size:25px;
  cursor: pointer;
  color:#2f72da;
  position:absolute;
  top:50%;
  transform:translateY(-50%);
  right: 8px;
}
  .card-c label{
    color: #fff;
  }
  .card-c .text-area input{
    color: #fff;
    padding-right:50px;
    
  }
