    box-shadow: 0px 3px 24px -8px rgba(0, 0, 0, 0.75);
  } */
  
  .name-field {
    margin-bottom: 40px;
  }
  .chat-text{
    font-family: monospace;
    color: rgb(67 66 66);
    font-size: 13px;
  }
  /* button {
    margin-top: 20px;
    padding: 10px;
    background: transparent;
    border-radius: 5px;
  } */
  
  .render-chat {
    background: url(https://web.whatsapp.com/img/bg-chat-tile-light_04fcacde539c58cca6745483d4858c52.png);
    /* background-repeat: repeat; */
    /* background-size: cover; */
    width:100%;
    min-height:33rem;
    max-height:350px;
    overflow: auto;
    border-radius: 5px;
    padding: 20px;
    box-shadow: 0px 3px 24px -8px rgba(0, 0, 0, 0.75);
  }
  .card-c form{
    width:100%;
    position: relative;
    background: #1353951f;
  }


  form{
    width:100%;
  }
  .message{
    width:100%;
  }
  
  h3 {
    color: #2f72da;
  }
  
  span {
    color: rgb(221, 221, 221);
  }
  
  @media only screen and (max-width:467px){
    .chat-card{
      width: 99%;
    }
  }