.player-container {
  height: 400px;
  /* width: 100%; */
  padding-top: 22.5px;
  position: relative;
  top: 70px;
  left: 0;
  color: #01201e;  /* fallback for old browsers */
  text-shadow: 2px 2px 5px black;
  display: flex;
  justify-content: center;
  font-size: 40px;
  border-radius:20px;
  z-index: 1;
}

.player-cross {
  color:white;
  margin-left: 18.5px;
  font-size: 68px;
  transform: scale(1);
  cursor: pointer;
  transition:transform 0.3s;
}
.player-cross:hover{
  transform: scale(1.2);
}
.player-container iframe{
  width: 400px;
  height: 226px;
  /* border-radius:50% 50% 50% 50% / 20% 20% 80% 80%; */
  /* box-shadow:rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px; */
}
.player-container > div{
  width:auto!important;
}

@media only screen and (max-width: 600px) {
  iframe {
    width: 400px;
    height: 226px;
    display: flex;
    align-items: right;
    justify-content: right;
    margin: auto;
    border-radius:"50% 50% 50% 50% / 20% 20% 80% 80% ";
    box-shadow:" rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px";
  
  }
  .player-container {
    /* width: 100%; */
    height: 300px;
    top: 36px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    position: relative;
  }
  #video-player-icons {
    margin-left: 20px;
    margin-right: 20px;
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
  }
}

#video-player-icons {
  width:100%;
  position: absolute;
  display: flex;
  justify-content:space-around;
  top:50%;
  transform: translateY(-50%);
  transition:opacity 0.5s;
  filter:blur(0.5px);
  opacity: 0;
}
#video-player-icons:hover{
opacity: 0.8;
}
#video-player-icons > div{
  margin:auto 0;
}
#video-player-icons .fa-heart{
  font-size: 68px;
}



.wrapper {
  height: 35vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
