.profile {
  border: 5px solid #ffffffb8;
  border-style: ridge;
  background: linear-gradient(#141414bf, #000000 100%, #00000054 100%, #ffffff);
  padding: 39px;
  border-radius: 15px;
  margin-top: 70px;
  margin-bottom: 70px !important;
  color: rgb(236, 236, 236);
}

.desc-form-container {
  width: 370px;
}
.profile .font-italic {
  color:rgb(221, 221, 221);
}
.icon-div {
  width: 40px;
  max-width: 40px;
  padding-left: 10px;
}

@media only screen and (max-width: 400px) {
  .desc-form-container {
    width: 100%;
  }
}
