.video{
  padding:10px;
}
.video-thumbnail{
  border:2px solid rgba(255, 255, 255, 0.582);
  border-radius: 15px;
}
.video-thumbnail img{
  width: 100%;
border-radius:15px;
}
.video-panel {
  border-radius:15px;
  position: absolute;
  width: 322px;
  height: 182px;
  background: #444;
  color: white;
  text-shadow: 2px 2px 5px black;
  transition: opacity 0.3s, box-shadow 0.3s;
  opacity: 0;
}

.video-panel:hover {
  opacity: 0.85;
  box-shadow: 0 0 200px #000 inset;
  animation-duration: 0.3s;
}

.panel-title {
  text-overflow: ellipsis;
  overflow: hidden;
  height: 50px;
}

.panel-icons {
  font-size: 40px;
  cursor: pointer;
}

.panel-user > .profile-link {
  color: wheat;
}

.panel-user > .profile-link:hover {
  color: white;
  text-decoration: none;
}
